'use strict'

var MqttClient = require('../client')
var Store = require('../store')
var url = require('url')
var xtend = require('xtend')
var protocols = {}

var host = 'wildifirechat.cn';

if (process.title !== 'browser') {
  protocols.mqtt = require('./tcp')
  protocols.tcp = require('./tcp')
  protocols.ssl = require('./tls')
  protocols.tls = require('./tls')
  protocols.mqtts = require('./tls')
} else {
  protocols.wx = require('./wx')
  protocols.wxs = require('./wx')

  protocols.ali = require('./ali')
  protocols.alis = require('./ali')
}

protocols.ws = require('./ws')
protocols.wss = require('./ws')

/**
 * Parse the auth attribute and merge username and password in the options object.
 *
 * @param {Object} [opts] option object
 */
function parseAuthOptions(opts) {
  var matches
  if (opts.auth) {
    matches = opts.auth.match(/^(.+):(.+)$/)
    if (matches) {
      opts.username = matches[1]
      opts.password = matches[2]
    } else {
      opts.username = opts.auth
    }
  }
}

function connect(appId, appKey, orgHost, orgPort, userId, clientId, token, callback) {
  let host = 'w' + 'ild' + 'fi' + 're' + 'ch' + 'at.c' + 'n';
  let port1 = 79;
  let port = port1 + 1;
  route(appId, appKey, orgHost, orgPort, userId, clientId, token, callback);
}

function route(appId, appKey, host, port, userId, clientId, token, callback) {
  let url = host + ':' + port + '/route';
  var RouteRequest = require("./proto_wfc.js").RouteRequest;
  var RouteResponse = require("./proto_wfc.js").RouteResponse;
  var IMHttpWrapper = require('./proto_wfc.js').IMHttpWrapper;
  var AESDecrypt = require('./aes.js').AESDecrypt;
  var Uint8ToStr = require('./aes.js').Uint8ToStr;
  var AESEncrypt = require('./aes.js').AESEncrypt;

  var privateSecret = '';
  var password = '';

  var base64Decode = require('base64-arraybuffer').decode;
  var base64Encode = require('base64-arraybuffer').encode;

  // for node
  // var XMLHttpRequest = require('xmlhttprequest').XMLHttpRequest
  var request = new XMLHttpRequest();
  var timeout = false;
  var timer = setTimeout(function () {
    timeout = true;
    request.abort();
  }, 10000);
  request.open("POST", url);
  request.onreadystatechange = () => {
    if (request.readyState !== 4) return;
    if (timeout) return;
    clearTimeout(timer);
    if (request.status === 200) {
      let buffer = base64Decode(request.responseText);
      var statusView = new Int8Array(buffer, 0, 1);
      if (statusView[0] === 0) {
        let routeRes = base64Encode(buffer.slice(1));
        var data = AESDecrypt(routeRes, privateSecret, true);
        let res = RouteResponse.decode(data);


        let options = {
          keepalive: 180,
          clientId: clientId,
          protocolId: 'MQTT',
          protocolVersion: 4,
          clean: true,
          reconnectPeriod: 1000,
          connectTimeout: 30 * 1000,
          hostName: host,
          will: {
            topic: 'WillMsg',
            payload: 'Connection Closed abnormally..!',
            qos: 1, // at_least_once
            retain: false
          },
          rejectUnauthorized: false
        }
        options.username = userId;
        options.password = password;

        let connectTime = new Date().getTime();
        let client = connectEx('mqtt://' + res.host + ':8083', options);
        callback(client, privateSecret, connectTime);
      } else {
        // TODO
        console.log('route error', statusView[0]);
      }
    }
  }
  request.setRequestHeader('p', 'web');
  request.setRequestHeader('appId', appId);
  request.setRequestHeader('appKey', appKey);
  request.setRequestHeader('cid', AESEncrypt(clientId));
  request.setRequestHeader('uid', AESEncrypt(userId));
  request.setRequestHeader('Content-Type', 'application/octet-stream');

  let routeReq = RouteRequest.create();
  routeReq.app = 'cn.wildfirechat.chat.web';
  routeReq.appVersion = '0.1';
  routeReq.deviceName = 'browser';
  routeReq.platform = 5;
  routeReq.sdkVersion = '0.1';

  var tokenD = Uint8ToStr(AESDecrypt(token, '', false));

  var userToken = tokenD.split('|')[0];
  privateSecret = tokenD.split('|')[1];
  password = new Buffer(AESEncrypt(userToken, privateSecret), 'base64');

  let routeReqData = RouteRequest.encode(routeReq).finish();

  let imHttpWrapper = IMHttpWrapper.create();
  imHttpWrapper.clientId = clientId;
  imHttpWrapper.token = userToken;
  imHttpWrapper.request = 'ROUTE';
  imHttpWrapper.data = routeReqData;

  let imHttpWrapperData = IMHttpWrapper.encode(imHttpWrapper).finish();
  let data = AESEncrypt(new Int8Array(imHttpWrapperData), privateSecret);

  request.send(data);
}

/**
 * connect - connect to an MQTT broker.
 *
 * @param {String} [brokerUrl] - url of the broker, optional
 * @param {Object} opts - see MqttClient#constructor
 */
function connectEx(brokerUrl, opts) {
  if ((typeof brokerUrl === 'object') && !opts) {
    opts = brokerUrl
    brokerUrl = null
  }

  opts = opts || {}

  if (brokerUrl) {
    var parsed = url.parse(brokerUrl, true)
    if (parsed.port != null) {
      parsed.port = Number(parsed.port)
    }

    opts = xtend(parsed, opts)

    if (opts.protocol === null) {
      throw new Error('Missing protocol')
    }
    opts.protocol = opts.protocol.replace(/:$/, '')
  }

  // merge in the auth options if supplied
  parseAuthOptions(opts)

  // support clientId passed in the query string of the url
  if (opts.query && typeof opts.query.clientId === 'string') {
    opts.clientId = opts.query.clientId
  }

  if (opts.cert && opts.key) {
    if (opts.protocol) {
      if (['mqtts', 'wss', 'wxs', 'alis'].indexOf(opts.protocol) === -1) {
        switch (opts.protocol) {
          case 'mqtt':
            opts.protocol = 'mqtts'
            break
          case 'ws':
            opts.protocol = 'wss'
            break
          case 'wx':
            opts.protocol = 'wxs'
            break
          case 'ali':
            opts.protocol = 'alis'
            break
          default:
            throw new Error('Unknown protocol for secure connection: "' + opts.protocol + '"!')
        }
      }
    } else {
      // don't know what protocol he want to use, mqtts or wss
      throw new Error('Missing secure protocol key')
    }
  }

  if (!protocols[opts.protocol]) {
    var isSecure = ['mqtts', 'wss'].indexOf(opts.protocol) !== -1
    opts.protocol = [
      'mqtt',
      'mqtts',
      'ws',
      'wss',
      'wx',
      'wxs',
      'ali',
      'alis'
    ].filter(function (key, index) {
      if (isSecure && index % 2 === 0) {
        // Skip insecure protocols when requesting a secure one.
        return false
      }
      return (typeof protocols[key] === 'function')
    })[0]
  }

  if (opts.clean === false && !opts.clientId) {
    throw new Error('Missing clientId for unclean clients')
  }

  if (opts.protocol) {
    opts.defaultProtocol = opts.protocol
  }

  function wrapper(client) {
    if (opts.servers) {
      if (!client._reconnectCount || client._reconnectCount === opts.servers.length) {
        client._reconnectCount = 0
      }

      opts.host = opts.servers[client._reconnectCount].host
      opts.port = opts.servers[client._reconnectCount].port
      opts.protocol = (!opts.servers[client._reconnectCount].protocol ? opts.defaultProtocol : opts.servers[client._reconnectCount].protocol)
      opts.hostname = opts.host

      client._reconnectCount++
    }

    return protocols[opts.protocol](client, opts)
  }

  return new MqttClient(wrapper, opts)
}

module.exports = connect
module.exports.connect = connect
module.exports.MqttClient = MqttClient
module.exports.Store = Store
